import * as React from 'react';
import clsx from 'clsx';
import TableContainer from '@material-ui/core/TableContainer';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableRow from '@material-ui/core/Tablerow';
import TableCell from '@material-ui/core/TableCell';
import IconButton from '@material-ui/core/IconButton';
import { makeStyles, createStyles } from '@material-ui/core/styles';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExpandLessIcon from '@material-ui/icons/ExpandLess';
import { SortKey } from '../../utils/AppState';
import { useAppState, useDispatch } from '../../utils/AppStateContext';
import { Edge, Mode, Tab } from '../../types';

interface Props {
  edge: Edge;
  mode: Mode;
  tab: Tab;
}

function ListItemTable({ edge, mode, tab }: Props) {
  return (
    <TableContainer>
      <Table size="small">
        {tab === 'pl' ? (
          <PLTable edge={edge} mode={mode} />
        ) : tab === 'bs' ? (
          <BSTable edge={edge} mode={mode} />
        ) : tab === 'revenue' ? (
          <RevenueTable edge={edge} mode={mode} />
        ) : tab === 'expense' ? (
          <ExpenseTable edge={edge} mode={mode} />
        ) : (
          <AttdTable edge={edge} mode={mode} />
        )}
      </Table>
    </TableContainer>
  );
}

export default ListItemTable;

function PLTable({ edge, mode }: Pick<Props, 'edge' | 'mode'>) {
  const { node } = edge;
  const [open, _toggleOpen] = useCollapsable(false);
  return (
    <TableBody>
      <DataTableRow label="" value={<strong>{node.revenue}</strong>} mode={mode} sortableKey="revenue" />
      <DataTableRow label="" value={node.expense} mode={mode} sortableKey="expense" />
      <DataTableRow label="" value={node.op_profit} mode={mode} sortableKey="op_profit" />
      <DataTableRow label="" value={node.profit} open={open} toggleOpen={_toggleOpen} selected mode={mode} sortableKey="profit" />
      {open ? (
        <>
          <DataTableRow label="" value={node.no_rev} inset mode={mode} sortableKey="no_rev" />
          <DataTableRow label="" value={node.no_exp} inset mode={mode} sortableKey="no_exp" />
          <DataTableRow label="" value={node.ordinary_profit} inset selected mode={mode} sortableKey="ordinary_profit" />
          <DataTableRow label="" value={node.sp_rev} inset mode={mode} sortableKey="sp_rev" />
          <DataTableRow label="" value={node.sp_exp} inset mode={mode} sortableKey="sp_exp" />
          <DataTableRow label="" value={node.tax} inset mode={mode} />
        </>
      ) : null}
      {(node.year ?? 0) > 2017 ? (
        <DataTableRow label="" value={node.related_revenue || '-'} mode={mode} sortableKey="related_revenue" />
      ) : null}
    </TableBody>
  );
}

function BSTable({ edge, mode }: Pick<Props, 'edge' | 'mode'>) {
  const { node } = edge;
  const [assetsOpen, toggleAssetsOpen] = useCollapsable(false);
  const [liabilitiesOpen, toggleLiabilitiesOpen] = useCollapsable(false);
  const [worthOpen, toggleWorthOpen] = useCollapsable(false);

  return (
    <TableBody>
      <DataTableRow
        label=" ()"
        value={node.assets}
        open={assetsOpen}
        toggleOpen={toggleAssetsOpen}
        mode={mode}
        sortableKey="assets"
      />
      {assetsOpen ? (
        <>
          <DataTableRow label="" value={node.curr_assets} inset mode={mode} />
          <DataTableRow label="" value={node.fixed_assets} inset mode={mode} />
        </>
      ) : null}
      <DataTableRow
        label=" ()"
        value={node.liabilities}
        open={liabilitiesOpen}
        toggleOpen={toggleLiabilitiesOpen}
        mode={mode}
        sortableKey="liabilities"
      />
      {liabilitiesOpen ? (
        <>
          <DataTableRow label="" value={node.curr_liabilities} inset mode={mode} />
          <DataTableRow label="" value={node.fixed_liabilities} inset mode={mode} />
        </>
      ) : null}
      <DataTableRow
        label=" ()"
        value={<strong>{node.net_worth}</strong>}
        selected
        open={worthOpen}
        toggleOpen={toggleWorthOpen}
        mode={mode}
        sortableKey="net_worth"
      />
      <DataTableRow label="" value={node.capital_stock} inset mode={mode} sortableKey="capital_stock" />
      {worthOpen ? (
        <>
          <DataTableRow label="" value={node.capital_surplus} inset mode={mode} />
          <DataTableRow label="" value={node.retained_earnings} inset mode={mode} />
        </>
      ) : null}
    </TableBody>
  );
}

function RevenueTable({ edge, mode }: Pick<Props, 'edge' | 'mode'>) {
  const { node } = edge;
  return (
    <TableBody>
      <DataTableRow label="" value={<strong>{node.revenue}</strong>} selected mode={mode} sortableKey="revenue" />
      <DataTableRow label="" value={node.sponsor} mode={mode} sortableKey="sponsor" />
      <DataTableRow label="" value={node.ticket} mode={mode} sortableKey="ticket" />
      <DataTableRow label="J" value={node.broadcast} mode={mode} sortableKey="broadcast" />
      {(node.year ?? 0) > 2010 ? (
        <DataTableRow label="" value={node.academy_rev} mode={mode} sortableKey="academy_rev" />
      ) : null}
      {(node.year ?? 0) > 2015 ? <DataTableRow label="" value={node.goods_rev} mode={mode} sortableKey="goods_rev" /> : null}
      <DataTableRow label="" value={node.other_revs} mode={mode} sortableKey="other_revs" />
    </TableBody>
  );
}

function ExpenseTable({ edge, mode }: Pick<Props, 'edge' | 'mode'>) {
  const { node } = edge;
  return (
    <TableBody>
      <DataTableRow label="" value={<strong>{node.expense}</strong>} selected mode={mode} sortableKey="expense" />
      <DataTableRow label="" value={<strong>{node.salary}</strong>} mode={mode} sortableKey="salary" />
      {(node.year ?? 0) < 2011 ? <DataTableRow label="()" value={node.manage_exp} mode={mode} /> : null}
      {(node.year ?? 0) > 2010 ? (
        <>
          <DataTableRow label="" value={node.game_exp} mode={mode} sortableKey="game_exp" />
          <DataTableRow label="" value={node.team_exp} mode={mode} sortableKey="team_exp" />
          <DataTableRow label="" value={node.academy_exp} mode={mode} sortableKey="academy_exp" />
          <DataTableRow label="" value={node.women_exp} mode={mode} sortableKey="women_exp" />
        </>
      ) : null}
      {(node.year ?? 0) > 2015 ? <DataTableRow label="" value={node.goods_rev} mode={mode} sortableKey="goods_exp" /> : null}
      <DataTableRow
        label={
          (node.year ?? 0) < 2011 ? '' : (node.year ?? 0) < 2016 ? '()' : ''
        }
        value={node.sga}
        mode={mode}
        sortableKey="sga"
      />
    </TableBody>
  );
}

function AttdTable({ edge, mode }: Pick<Props, 'edge' | 'mode'>) {
  const { node } = edge;
  const [gamesOpen, toggleGamesOpen] = useCollapsable(false);
  const [attdOpen, toggleAttdOpen] = useCollapsable(false);
  return (
    <TableBody>
      <DataTableRow label="" value={<strong>{node.ticket ?? '-'}</strong>} selected mode={mode} sortableKey="ticket" />
      <DataTableRow
        label=""
        value={Math.round((node.league_attd ?? 1) / (node.league_games ?? 1))}
        mode={mode}
        sortableKey="average_attd"
      />
      <DataTableRow label="" value={node.all_games} open={gamesOpen} toggleOpen={toggleGamesOpen} mode={mode} />
      {gamesOpen ? (
        <>
          <DataTableRow label="" value={node.league_games} inset mode={mode} />
          <DataTableRow label="" value={node.leaguecup_games || '-'} inset mode={mode} />
          <DataTableRow label="ACL" value={node.acl_games || '-'} inset mode={mode} />
          <DataTableRow label="" value={node.po_games || '-'} inset mode={mode} />
          <DataTableRow label="U-23" value={node.second_games || '-'} inset mode={mode} />
        </>
      ) : null}
      <DataTableRow
        label=""
        value={node.all_attd}
        open={attdOpen}
        toggleOpen={toggleAttdOpen}
        mode={mode}
        sortableKey="all_attd"
      />
      {attdOpen ? (
        <>
          <DataTableRow label="" value={node.league_attd} inset mode={mode} sortableKey="league_attd" />
          <DataTableRow label="" value={node.leaguecup_attd || '-'} inset mode={mode} />
          <DataTableRow label="ACL" value={node.acl_attd || '-'} inset mode={mode} />
          <DataTableRow label="" value={node.po_attd || '-'} inset mode={mode} />
          <DataTableRow label="U-23" value={node.second_attd || '-'} inset mode={mode} />
        </>
      ) : null}
      <DataTableRow
        label=""
        value={node.ticket && node.all_attd ? Math.round((node.ticket * 1000000) / node.all_attd) : '-'}
        mode={mode}
        sortableKey="unit_price"
      />
    </TableBody>
  );
}

const useDataTableRowStyles = makeStyles((theme) =>
  createStyles({
    selected: {
      background: theme.palette.type === 'dark' ? theme.palette.background.paper : theme.palette.grey[200],
    },
    labelInset: {
      paddingLeft: theme.spacing(5),
    },
    sortable: {
      '&:hover': {
        textDecoration: 'underline',
        cursor: 'pointer',
      },
    },
    current: {
      color: theme.palette.secondary.main,
      fontWeight: theme.typography.fontWeightBold,
    },
  })
);

interface DataTableRowProps {
  open?: boolean;
  toggleOpen?: () => void;
  selected?: boolean;
  inset?: boolean;
  sortableKey?: SortKey;
  mode: Mode;
  label: React.ReactNode;
  value: React.ReactNode | null;
}

function DataTableRow({ label, mode, value, open, toggleOpen, inset = false, selected = false, sortableKey }: DataTableRowProps) {
  const { sortKey } = useAppState();
  const current = sortKey === sortableKey;
  const dispatch = useDispatch();
  const classes = useDataTableRowStyles();
  const _onClick = () => {
    if (mode === 'club' || !sortableKey) return;
    if (current) {
      dispatch({ type: 'TOGGLE_SORTASC' });
    } else {
      dispatch({ type: 'CHANGE_SORTKEY', sortKey: sortableKey });
    }
  };

  return (
    <TableRow className={clsx({ [classes.selected]: selected })}>
      <TableCell padding="checkbox">
        {typeof open === 'boolean' && typeof toggleOpen === 'function' ? (
          <IconButton aria-label="expand row" size="small" onClick={toggleOpen}>
            {open ? <ExpandLessIcon /> : <ExpandMoreIcon />}
          </IconButton>
        ) : null}
      </TableCell>
      <TableCell className={clsx({ [classes.labelInset]: inset })} component="th" scope="row">
        <span
          className={clsx({ [classes.sortable]: mode === 'year' && !!sortableKey, [classes.current]: mode === 'year' && current })}
          onClick={_onClick}
        >
          {label}
        </span>
      </TableCell>
      <TableCell align="right">{value ?? '-'}</TableCell>
    </TableRow>
  );
}

function useCollapsable(initialOpen: boolean): [boolean, () => void] {
  const [open, setOpen] = React.useState(initialOpen);
  const _setOpen = () => {
    setOpen(!open);
  };
  return [open, _setOpen];
}
